import React, { Component } from 'react';
import { ProjectContainer, Content, CloseButtonContainer, InfoSet, ProjectImage, DescContainer, DescItem, DescGrid, ProjectDetailContainer } from './Project.style'
import FontAwButton from '../FontAwButton/FontAwButton'

class Project extends Component {

  renderProjectDesc(project, center) {
    let justifyContent =  center ? 'center' : 'flex-start';
    const projDes = project.description || [];
    const descLines = projDes.map((line, index) =>
      <DescItem key={index}>{line}</DescItem>
    )
    return (
      <DescContainer
        column
        justify={justifyContent}
      >
        <DescGrid>
          {descLines}
          { project.repoUrl &&
          <DescItem key={-1}>
            <a
              target='_blank'
              href={project.repoUrl}>Check out the GitHub repo <i className='fa fa-github' aria-hidden='true'></i></a>
          </DescItem>}
        </DescGrid>
      </DescContainer>
    );
  }

  render() {
    const title = this.props.project.title || 'Project'
    const projectSwitch = this.props.projectSwitch;
    // const description = this.props.project.description || [];
    const image = this.props.project.imageUrl || 'default-proj-img.png';
    return (
      <ProjectContainer
        hide={!projectSwitch}
      >
        <Content>
        <CloseButtonContainer onClick={() => this.props.closeProject(false)}>
          <FontAwButton
                        class={'fa fa-times'}/>
        </CloseButtonContainer>
        <h1>{title}</h1>
        <hr/>
        <ProjectDetailContainer
          row
          justify={'center'}
          alignItems={'flex-start'}
        >
          {this.props.project.imageUrl &&
            <InfoSet><ProjectImage src={require(`../../assets/img/${image}`)}/></InfoSet>
          }
          <InfoSet>{this.renderProjectDesc(this.props.project, this.props.project.imageUrl)}</InfoSet>
        </ProjectDetailContainer>
        <FontAwButton class={'fa fa-angle-double-up'}/>
        </Content>
      </ProjectContainer>
    );
  }
}

export default Project;



